* {
  padding: 0;
  margin: 0;
  font-family: "Jost", sans-serif;
}

body {
  background: #a7a59e;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  height: 100vh;
  width: 100vw;
  font-family: "Jost", sans-serif;
}

html {
  background-color: #eee;
  font-size: 100%;
  /* height: 100%; */
}

.inputField{
   font-size: 1.2rem;
   font-weight: 600;
}

.box {
  width: 25vw;
  height: 50vh;
  border-radius: 0.5rem;
  box-shadow: 0 0.2rem 3rem rgba(0, 0, 0, 0.2);
  background: #a5bbdd;
  position: relative;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
  min-width: 20rem;
  min-height: 33rem;
}

.info {
  margin-top: 27%;
}

.location {
  font-size: 4rem;
}

.tempmin_max{
  margin-top: 8%;
}

p {
  margin-top: 15%;
  font-size: 2.5rem;
  font-weight: 500;
  font-family: Arial, Helvetica, sans-serif;
}

.inputField {
  margin-top: 3%;
  height: 60px;
  width: 50%;
  border-width: 3px 4px;
  border-radius: 40px;
}

.wave {
  opacity: 0.3;
  position: absolute;
  top: 120%;
  left: 50%;
  background: white;
  width: 50rem;
  height: 50rem;
  margin-left: -25rem;
  margin-top: -20rem;
  transform-origin: 50% 48%;
  border-radius: 43%;
  animation: drift 3000ms infinite linear;
  z-index: 1;
}

.wave .-three {
  animation: drift 5000ms infinite linear;
  z-index: 2 !important;
  opacity: 0.2;
}

.wave .-two {
  animation: drift 7000ms infinite linear;
  opacity: 0.1;
  z-index: 3 !important;
}

.box:after {
  content: "";
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 11;
  transform: translate3d(0, 0, 0);
}

@keyframes drift {
  from {
    transform: rotate(0deg);
  }

  from {
    transform: rotate(360deg);
  }
}